<template>
  <canvas id="paper"></canvas>
  <div
    class="toolBar"
    v-show="toolBarVisible"
    :style="{
      left: toolBarPosition.x + 'px',
      top: toolBarPosition.y + 'px',
    }"
    ref="toolBarRef"
  >
    <div
      v-for="item in toolBar"
      class="toolBar-item"
      :title="item.title"
      @click="item.func"
      :style="{
        border: item.key === currentTool ? '1px solid red' : '',
      }"
    >
      <span :class="'ksdhy ' + item.icon"></span>
    </div>
  </div>
  <div
    :style="{
      left: rgbPosition.x + 'px',
      top: rgbPosition.y + 'px',
    }"
  >
    {{ rgbInfo }}
  </div>
</template>

<script setup>
import paper from "paper";
import { nextTick, onMounted, ref } from "vue";
import { exportImageInfo, getImage } from "@/electron.js";
import emitter from "./mitt.js";
// text
let text;

// Path
let path;

// 
let raster;

// 
let originalImageData;

// xyxHalfyHalf
let moveConditions = {
  0: {
    x: [0, 1, 2],
    y: [0, 6, 7],
    xHalf: [3, 7],
    yHalf: [1, 5],
    cursor: "ne-resize",
  },
  1: {
    x: [1, 0, 2],
    y: [],
    xHalf: [3, 7],
    yHalf: [],
    cursor: "ew-resize",
  },
  2: {
    x: [2, 0, 1],
    y: [2, 3, 4],
    xHalf: [3, 7],
    yHalf: [1, 5],
    cursor: "nw-resize",
  },
  3: {
    x: [],
    y: [3, 2, 4],
    xHalf: [],
    yHalf: [1, 5],
    cursor: "ns-resize",
  },
  4: {
    x: [4, 5, 6],
    y: [4, 2, 3],
    xHalf: [3, 7],
    yHalf: [1, 5],
    cursor: "ne-resize",
  },
  5: {
    x: [5, 4, 6],
    y: [],
    xHalf: [3, 7],
    yHalf: [],
    cursor: "ew-resize",
  },
  6: {
    x: [6, 4, 5],
    y: [6, 0, 7],
    xHalf: [3, 7],
    yHalf: [1, 5],
    cursor: "nw-resize",
  },
  7: {
    x: [],
    y: [7, 0, 6],
    xHalf: [],
    yHalf: [1, 5],
    cursor: "ns-resize",
  },
};

const toolBarPosition = ref({ x: 0, y: 0 });
const toolBarVisible = ref(false);
const toolBarRef = ref(null);
const rgbInfo = ref("");
const rgbPosition = ref({ x: 0, y: 0 });
let defaultTool = null;
let currentTool = ref("");

const switchTool = (key) => {
  if (currentTool.value === key) {
    currentTool.value = "";
    defaultTool && defaultTool.active();
  } else {
    currentTool.value = key;
  }
};

const toolBar = [
  // {
  //   icon: "icon-jiantou_youshang",
  //   title: "",
  //   key: "drawArrow",
  //   func: () => {
  //     drawArrow("red");
  //     switchTool("drawArrow");
  //   },
  //   tool: null,
  // },
  {
    icon: "icon-guding",
    title: "",
    key: "top",
    func: () => {
      exportImageInfo(path.bounds, originalImageData, true);
    },
    tool: null,
  },
  {
    icon: "icon-ai210",
    title: "",
    key: "finish",
    func: () => {
      // ArrayBuffer
      exportImageInfo(path.bounds, originalImageData);
    },
    tool: null,
  },
];

/**
 * 
 * @param path
 */
const printBoundsInfo = (path) => {
  let leftTop = path.bounds.topLeft;
  // 
  let content = `${path.bounds.width}x${path.bounds.height}`;
  let fontsize = 16;
  if (text) {
    text.remove();
  }
  text = new paper.PointText({
    point: [leftTop.x, leftTop.y - fontsize],
    content,
    fillColor: "white",
    fontSize: fontsize,
  });
};

/**
 * path.RectAnglesegmentscontrol segment
 * @param path
 */
const insertControlSegment = (path) => {
  //   segment,
  let segments = path.segments;
  let left = segments[0].point;
  let right = segments[2].point;
  let top = segments[1].point;
  let bottom = segments[3].point;
  let leftTop = new paper.Point((left.x + top.x) / 2, (left.y + top.y) / 2);
  let rightTop = new paper.Point((right.x + top.x) / 2, (right.y + top.y) / 2);
  let leftBottom = new paper.Point(
    (left.x + bottom.x) / 2,
    (left.y + bottom.y) / 2,
  );
  let rightBottom = new paper.Point(
    (right.x + bottom.x) / 2,
    (right.y + bottom.y) / 2,
  );
  path.insert(1, leftTop);
  path.insert(3, rightTop);
  path.insert(5, rightBottom);
  path.insert(7, leftBottom);
};

/**
 * control point
 * @param point
 * @return {paper.Item}
 */
const createControlPoint = (point) => {
  const length = 6;
  const color = new paper.Color(0, 0, 1);
  return new paper.Path.Rectangle({
    from: [point.x - length / 2, point.y - length / 2],
    to: [point.x + length / 2, point.y + length / 2],
    fillColor: color,
  });
};

/**
 * 
 * @param path
 */
const updateToolBarPosition = (path) => {
  let bottomRight = path.bounds.bottomRight;
  let toolBarWidth = toolBarRef.value.getClientRects()[0].width;
  let toolBarHeight = toolBarRef.value.getClientRects()[0].height;
  let x = 0;
  let y = 0;
  // 
  let clientHeight = document.documentElement.clientHeight;
  let pathBottom = bottomRight.y;
  y =
    clientHeight - pathBottom < toolBarHeight
      ? bottomRight.y - toolBarHeight
      : bottomRight.y;

  // 
  // 
  x = path.bounds.right - toolBarWidth;
  if (path.bounds.left < toolBarWidth) {
    x = path.bounds.left;
  }

  toolBarPosition.value = { x, y };
};

/**
 * 
 * @param path
 */
const createToolBar = (path) => {
  toolBarVisible.value = true;
  nextTick(updateToolBarPosition.bind(null, path));
};

/**
 * 
 */
const createClipRect = () => {
  // rect
  let fullRect = new paper.Path.Rectangle({
    from: [0, 0],
    to: [paper.view.bounds.width, paper.view.bounds.height],
  });
  fullRect.strokeColor = new paper.Color("blue");
  fullRect.strokeWidth = 2;
  // 
  let leftTop;
  defaultTool = new paper.Tool();
  defaultTool.onMouseDown = function (event) {
    leftTop = event.point;
    path = new paper.Path.Rectangle({
      from: event.point,
      to: event.point,
      strokeColor: "blue",
    });
    fullRect.visible = false;
  };
  defaultTool.onMouseDrag = function (event) {
    path.remove();
    path = new paper.Path.Rectangle({
      from: leftTop,
      to: event.point,
      strokeColor: "blue",
      strokeWidth: 1,
    });
    printBoundsInfo(path);
  };
  defaultTool.onMouseUp = function (event) {
    if (path.bounds.width < 3 || path.bounds.height < 3) {
      text.remove();
      path.remove();
      fullRect.visible = true;
      return;
    }
    defaultTool.remove();

    // 
    createToolBar(path);

    // 
    insertControlSegment(path);

    // path
    path.fillColor = new paper.Color(0, 0, 1, 0.00000001);
    path.onMouseDrag = (event) => {
      document.body.style.cursor = "move";
      let delta = event.delta;
      path.position.x += delta.x;
      path.position.y += delta.y;

      // 
      if (path.bounds.topLeft.x <= 0) {
        path.position.x = path.bounds.width / 2 + 1;
      } else if (path.bounds.topLeft.y <= 0) {
        path.position.y = path.bounds.height / 2 + 1;
      } else if (path.bounds.bottomRight.x >= paper.view.bounds.width) {
        path.position.x = paper.view.bounds.width - path.bounds.width / 2 - 1;
      } else if (path.bounds.bottomRight.y >= paper.view.bounds.height) {
        path.position.y = paper.view.bounds.height - path.bounds.height / 2 - 1;
      }
      controlPointSegmentMap.forEach((item) => {
        item.controlPoint.position = item.segment.point;
        item.controlPoint.position = item.segment.point;
      });

      printBoundsInfo(path); // 
      updateToolBarPosition(path); // 
    };
    path.onMouseEnter = () => {
      document.body.style.cursor = "move";
    };
    path.onMouseLeave = () => {
      document.body.style.cursor = "default";
    };

    // controlpathsegmentscirclesegment
    let controlPointSegmentMap = [];

    // 
    path.segments.forEach((segment, index) => {
      let controlPoint = createControlPoint(segment.point);
      controlPointSegmentMap.push({
        index,
        controlPoint,
        segment,
      });
    });

    // 
    controlPointSegmentMap.forEach((item, index) => {
      item.controlPoint.onMouseDrag = (event) => {
        let delta = event.delta;
        let { x, y, xHalf, yHalf } = moveConditions[index];

        x.forEach((i) => {
          controlPointSegmentMap[i].controlPoint.position.x += delta.x;
          controlPointSegmentMap[i].segment.point.x += delta.x;
        });
        y.forEach((i) => {
          controlPointSegmentMap[i].controlPoint.position.y += delta.y;
          controlPointSegmentMap[i].segment.point.y += delta.y;
        });
        xHalf.forEach((i) => {
          controlPointSegmentMap[i].controlPoint.position.x += delta.x / 2;
          controlPointSegmentMap[i].segment.point.x += delta.x / 2;
        });
        yHalf.forEach((i) => {
          controlPointSegmentMap[i].controlPoint.position.y += delta.y / 2;
          controlPointSegmentMap[i].segment.point.y += delta.y / 2;
        });
        // 
        printBoundsInfo(path);
        // 
        updateToolBarPosition(path);
        document.body.style.cursor = moveConditions[index].cursor;
      };
      item.controlPoint.onMouseEnter = () => {
        document.body.style.cursor = moveConditions[index].cursor;
      };
      item.controlPoint.onMouseLeave = () => {
        document.body.style.cursor = "default";
      };
    });
  };
};
const drawArrow = (color) => {
  let tool = new paper.Tool();
  let arrow = null;
  let from = null;

  // path
  path.onMouseDrag = null;
  path.onMouseEnter = null;
  path.onMouseLeave = null;

  tool.onMouseDown = function (event) {
    event.stopPropagation();
    // path
    // if (path && path.contains(event.point)) {
    //   return;
    // }
    // 
    from = event.point;
  };

  tool.onMouseDrag = function (event) {
    event.stopPropagation();
    // path
    // if (path && path.contains(event.point)) {
    //   return;
    // }
    if (arrow) {
      arrow.remove(); // 
    }

    // 
    let end = event.point;

    const lineWidth = 5;

    // 
    let arrowSize = 20;

    // 
    let vector = end.subtract(from);
    let arrowVector = vector.normalize(arrowSize);

    // 
    let arrowLeft = arrowVector.rotate(135).add(end);
    let arrowRight = arrowVector.rotate(-135).add(end);

    // 
    let lineEnd = end.subtract(vector.normalize(arrowSize * 0.5)); // 

    // 
    let line = new paper.Path.Line(from, lineEnd);
    line.strokeWidth = lineWidth;
    line.strokeColor = color;

    // 
    let arrowHead = new paper.Path([end, arrowLeft, arrowRight]);
    arrowHead.closed = true;
    arrowHead.fillColor = color;

    //  Group
    arrow = new paper.Group([line, arrowHead]);
  };

  tool.onMouseUp = function (event) {
    event.stopPropagation();
    // 
    arrow = null;
  };
};

// rgb
const getPixelColor = (event) => {
  let tool = new paper.Tool();
  tool.on("mousemove", (event) => {
    let point = new paper.Point(event.point.x, event.point.y);
    let color = raster.getPixel(point);
    rgbInfo.value = `rgb(${color.red * 255}, ${color.green * 255}, ${
      color.blue * 255
    })`;
  });
  // tool.onMouseMove = (event) => {
  //   let point = new paper.Point(event.offsetX, event.offsetY);
  //   let color = raster.getPixel(point);
  //   rgbInfo.value = `rgb(${color.red * 255}, ${color.green * 255}, ${
  //     color.blue * 255
  //   })`;
  // };
};

emitter.on("image", async ({ base64, imageData }) => {
  // defaultTool && defaultTool.active();
  // 
  console.log("clear");
  paper.project.clear();
  toolBarVisible.value = false;
  originalImageData = imageData;
  raster = new paper.Raster(base64);
  raster.onLoad = () => {
    raster.fitBounds(paper.view.bounds);
    createClipRect();
    // getPixelColor();
  };
});
onMounted(async () => {
  paper.setup("paper");
  getImage();
});
</script>

